import React from 'react'
import PropTypes from 'prop-types'
import { useState} from 'react'


export default function TextForm(props) {

    const handleUpClick = ()=>{

        // console.log("UpperCase");
        let newText = text.toUpperCase();
        setText(newText);

    };
    
    const handleLowClick = ()=>{

        // console.log("LowerCase");
        let newText = text.toLowerCase();
        setText(newText);

    };

    const handleClrClick = ()=>{
        
        // console.log("Clear");
        let newText = "";
        setText(newText);

    };
    
    const handleCopy = ()=>{
        
        // console.log("Copy Text");

        // Method 1
        // let newText = document.getElementById("BoxArea");
        // newText.select();
        // navigator.clipboard.writeText(newText.value);

        // Method 2
        let newText = text;
        navigator.clipboard.writeText(newText);

    };
    
    const handleExtra = ()=>{

        // console.log("Remove Extra Spaces")
        // we are using regex in here
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "));

    };

    // whenever we fire an event listener we get event as the param for the function
    // we access the value by targeting the value of the event
    const handleOnChange = (event)=>{

        // console.log("OnChange");

        setText(event.target.value);
    };
    
    // this represents state and we use this
    // hooks helps to use class features without using the class based components
    // we'll use the setText function to change the value of the text variable
    // setText("New Text");

    const [text, setText] = useState('');
    
    return (
        <>
        <div className="container my-3">

            <h1>{props.title}</h1>

            <div className="mb-3">

                <textarea className="form-control" id="BoxArea" rows="5" value = {text} onChange={handleOnChange}></textarea>

            </div>

            <button type="button" className="btn btn-outline-primary mx-2" onClick={handleUpClick}>Convert to UpperCase</button>
            <button type="button" className="btn btn-outline-primary mx-2" onClick={handleLowClick}>Convert to LowerCase</button>
            <button type="button" className="btn btn-outline-primary mx-2" onClick={handleExtra}>Remove Extra Spaces</button>
            <button type="button" className="btn btn-outline-primary mx-2" onClick={handleCopy}>Copy Text</button>
            <button type="button" className="btn btn-outline-primary mx-2" onClick={handleClrClick}>Clear</button>

        </div>

        <div className="container my-3">
            <h2>Summary of the text you entered</h2>
            <p>
                <ol>
                <li>Total characters in the text :- {text.length}</li>
                <li>Total words in the text :- {text.split(' ').length}</li>
                <li>Approximated time required to read the text :- {text.split(' ').length*0.0080} minutes</li>
                </ol>
            </p>
        </div>

        </>
    )
};

TextForm.propTypes = {
    title:PropTypes.string.isRequired
};

TextForm.defaultProps = {
    title:"Enter the text to analyze it"
};